body, div.container-fluid, div.col-3, .row {
    margin: 0 !important;
    padding: 0 !important;
}

img.mainImage {
    position: absolute;
    height:75vh;
    width:100%;
    top: 0; left: 0;
    z-index: 4;
    -webkit-animation: slideshow 20s linear 0s infinite;
    animation: slideshow 20s linear 0s infinite;
  }
  
  img.mainImage:nth-child(2) {
    z-index: 3;
    -webkit-animation: slideshow 20s linear 4s infinite;
    animation: slideshow 20s linear 4s infinite;
  }
  
  img.mainImage:nth-child(3) {
     z-index: 2;
    -webkit-animation: slideshow 20s linear 8s infinite;
    animation: slideshow 20s linear 8s infinite;
  }
  
  img.mainImage:nth-child(4) {
    z-index: 1;
   -webkit-animation: slideshow 20s linear 12s infinite;
   animation: slideshow 20s linear 12s infinite;
 }

 img.mainImage:nth-child(5) {
    z-index: 1;
   -webkit-animation: slideshow 20s linear 16s infinite;
   animation: slideshow 20s linear 16s infinite;
 }

  
  div.bottom.container-fluid {
    position: absolute;
    margin: 0;
    padding:0;
    bottom: 0;
  }

  img.slideImage {
     display: block;
     height: 25vh;
     width: 100%;
     
 }

 img.slideImage {
     
 }

 @-webkit-keyframes mainImageZoom {
    0%   {}
    25% {-webkit-transform:scale(1.25);transform:scale(1.25);}
    50%   {-webkit-transform:scale(1.5);transform:scale(1.5);}
  100%    {-webkit-transform:scale(1.75);transform:scale(1.75);} 
}

 @keyframes mainImageZoom {
    0%   {}
    25% {-webkit-transform:scale(1.25);transform:scale(1.25);}
    50%   {-webkit-transform:scale(1.5);transform:scale(1.5);}
  100%    {-webkit-transform:scale(1.75);transform:scale(1.75);} 
}

@-webkit-keyframes slideshow {
  25% { opacity: 1;}
  33.33% { opacity: 0;} 
  91.66% { opacity: 0;}
  100% { opacity: 1;}
}
@keyframes slideshow {
  25% { opacity: 1;}
  33.33% { opacity: 0;} 
  91.66% { opacity: 0;}
  100% { opacity: 1;}
}



