

body { 
  background: #A9EDDA; 
  height: 100%; 
}

.news {
  background: #c8102e;
  -webkit-box-shadow: inset 0 -15px 30px rgba(0,0,0,0.4), 0 5px 10px rgba(0,0,0,0.5);
          box-shadow: inset 0 -15px 30px rgba(0,0,0,0.4), 0 5px 10px rgba(0,0,0,0.5);
  width: 100vw;
  height: 40vh;
  margin: 50px auto;
  padding: 3px;
  -webkit-user-select: none;
} 









img.scrollImage:hover { -webkit-animation-play-state: paused; animation-play-state: paused }
/* img.scrollImage span:hover+ul { animation-play-state: paused } */

img.scrollImage  {
  height: 35vh;
  width: 35vh;
  top: 7vh;
  padding: 20px;
  right: 110%;
  position: absolute;
  -webkit-animation: slideRight 36s linear 0s infinite;
          animation: slideRight 36s linear 0s infinite;
}

img.scrollImage:nth-child(2) {
  -webkit-animation: slideRight 36s linear 7s infinite;
          animation: slideRight 36s linear 7s infinite;
}

img.scrollImage:nth-child(3) {
  -webkit-animation: slideRight 36s linear 14s infinite;
          animation: slideRight 36s linear 14s infinite;
}

img.scrollImage:nth-child(4) {
  -webkit-animation: slideRight 36s linear 21s infinite;
          animation: slideRight 36s linear 21s infinite;
}

img.scrollImage:nth-child(5) {
  -webkit-animation: slideRight 36s linear 28s infinite;
          animation: slideRight 36s linear 28s infinite;
}

@-webkit-keyframes slideRight {
  0% {right: 105%}
  
  100% {right: -20%}
}

@keyframes slideRight {
  0% {right: 105%}
  
  100% {right: -20%}
}


